const getChecklistData = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw(`getChecklistData() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return { id: id, type: metaData.type, label: metaData.label }
}

const getTabLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getTabLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return { id: id, label: metaData.tabLabel }
}

const getLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return metaData.label
}


const getIntvLabel = (id) => {
    const metaData = INTV_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getIntvLabel() id: ${id} not found in INTV_META_DATA`)
    }
    return metaData.label
}

const getIntvData = (id) => {
    const metaData = INTV_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getIntvData() id: ${id} not found in INTV_META_DATA`)
    }
    return metaData
}

const deleteNestedIntvForm = (id) => {
    if (! ( id in NESTED_INTV_FORMS)) {
        throw(`deleteNestedIntvForm() id: "${id} not found in INTV_FORMS`)
    }
    delete NESTED_INTV_FORMS[id]
}


export const checkForDupIDs = () => {
    let IDs = {}
    CHECKLIST_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    INTV_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    const dupIDs = Array.from(Object.entries(IDs)).filter(([id, count]) => count > 1)
    if (dupIDs.length > 0) {
        console.error("Duplicate IDs found!")
        console.log(JSON.stringify(dupIDs, null, 4))
        process.exit(1)
    }
}

const namingConventionViolation = (itemType, prefix, rec) => {
    return rec.type === itemType && !rec.id.startsWith(prefix)
}

const getNamingConventionViolations = (itemType, prefix) => {
    const badRecs = CHECKLIST_META_DATA.filter((rec) => namingConventionViolation(itemType, prefix, rec))
    return badRecs.concat(INTV_META_DATA.filter((rec) => namingConventionViolation(itemType, prefix, rec)))
}


const reportViolations = (label, properPrefix, violations) => {
    if (violations.length > 0) {
        console.log(`
The following ${label} don't begin with the prefix "${properPrefix}"
--------------------------------------------------------------------------------`)
        violations.forEach((rec) => console.log(rec.id))
    }
}

const anyHaveValues = (...arrs) => {
    // returns if any of the arrays passed to this have lengths > 0
    // perhaps this should be written as a non-1-liner for clarity
    return arrs.map((arr) => arr.length > 0).filter(val => val === true).length > 0
}

export const enforceNamingConventions = () => {
    const badHeadings = getNamingConventionViolations(HEADING, "heading-")
    const badAssessments = getNamingConventionViolations(ASSESS, "assess-")
    const badRequiredActions = getNamingConventionViolations(REQURED, "required-action-")
    const badCriticalFailures = getNamingConventionViolations(CRIT_FAIL, "critical-failure-")
    const badIntvForms = getNamingConventionViolations(INTV_FORM, "intv-form-")
    const badCheckListPrompts = getNamingConventionViolations(CL_PROMPT, "cl-prompt-")
    const badPrompts = getNamingConventionViolations(PROMPT, "prompt-")
    const badIntvs = getNamingConventionViolations(INTERV, "intv-")
    const badAnswers = getNamingConventionViolations(ANSWER, "answer-")

    // disabled until I actually start renaming things
    // const badHeadings = []
    // const badAssessments = []
    // const badRequiredActions = []
    // const badCriticalFailures = []
    // const badIntvForms = []
    // const badCheckListPrompts = []
    // const badPrompts = []
    // const badIntvs = []
    // const badAnswers = []
    if (anyHaveValues(
            badHeadings,
            badAssessments,
            badRequiredActions,
            badCriticalFailures,
            badIntvForms,
            badCheckListPrompts,
            badPrompts,
            badIntvs,
            badAnswers)) {
        reportViolations("headings", "heading-", badHeadings)
        reportViolations("assessments", "assess-", badAssessments)
        reportViolations("required actions", "required-action-", badRequiredActions)
        reportViolations("critical failures", "critical-failure", badCriticalFailures)
        reportViolations("checklist prompts", "cl-prompt-", badCheckListPrompts)
        reportViolations("intervention forms", "intv-form-", badIntvForms)
        reportViolations("prompts", "prompt-", badPrompts)
        reportViolations("interventions", "intv-", badIntvs)
        reportViolations("answers", "answer-", badAnswers)
        process.exit(1)
    }
}

export const checkPrompts = () => {
    const prompts = INTV_META_DATA.filter((rec) => rec.type === PROMPT)
    const missingSelectionType = prompts.filter((rec) => !("selectionType" in rec))
    const badSelectionType = prompts.filter((rec) => ![ONE, OOM, NONE].includes(rec.selectionType))
    if (!!missingSelectionType.length || !!badSelectionType.length) {
        console.log("missingSelectionType", missingSelectionType)
        console.log("badSelectionType", badSelectionType)
        process.exit(1)
    }
}

export const checkAssessments = () => {
    const assessments = CHECKLIST_META_DATA.filter((rec) => rec.type === ASSESS)
    const missingTabLabels = assessments.filter((rec) => !("tabLabel" in rec))
    if (!!missingTabLabels.length) {
        console.log("Assessments missing tabLabels:", missingTabLabels)
        process.exit(1)
    }
}

const nestedForm = ({id, show = true }) => {
    const form = INTV_FORMS[id]
    if (form === undefined) {
        throw (`nestedForm() id: "${id} not found in INTV_FORMS`)
    }
    if (true !== show) {
        if (show.includes("(")) {
            const showIDs = show.replace(/\(/g, "")
                                .replace(/\)/g, "")
                                .replace(/OR/g, "")
                                .replace(/\s{2,}/g, " ")
                                .split(" ")
            showIDs.forEach((showID) => {
                const found = INTV_META_DATA.find((rec) => rec.id === showID)
                if (found === undefined) {
                    throw(`nestedForm() show: ${showID} not found in INTV_META_DATA`)
                }
            })
        } else {
            const showId = INTV_META_DATA.find((rec) => rec.id === show)
            if (showId === undefined) {
                throw (`nestedForm() show: ${show} not found in INTV_META_DATA`)
            }
        }
    }
    const rec = { id: `${form.id}`, type: "nested-form", label: `${form.label}`, show: show, checkedWhen: `${form.checkedWhen}` }
    // , children: form.children
    // console.log(rec)
    return rec
}


