const checkPromptOptions = () => {
    INTV_META_DATA.filter((rec) => [INTERV, ANSWER].includes(rec.type))
        .forEach((rec) => { })
}


const getPromptsAndAnswers = (formID) => {
    let prompts = INTV_META_DATA.filter((rec) => rec.type === PROMPT && rec.form === formID)
    // console.log(prompts)
    prompts = prompts.map((prompt) => {
        let newPrompt = Object.assign({}, prompt)
        const options = INTV_META_DATA.filter((rec) => rec.prompt === prompt.id)
        newPrompt.children = options
        return newPrompt
    })
    return prompts
}


